import React, { Component } from 'react'
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { useNavigate, useParams, Link } from 'react-router-dom';
import {updateListing} from "../../actions/listadosActions";


class EditListing extends Component {
    constructor(){
        super();
        this.state={
            listingId:"",
            title: "sdasa",
            description: "",
            location: {
                stateName: "",
                city: "",
            },
            category:"",
            price: "",
            images: [],
           // tags: "",
            userId:"",
        }
    }

    componentDidMount(){

        this.fetchListingInfo();
        console.log({listing:this.state})
    }

    fetchListingInfo= () => {
        fetch(`/api/listados/${this.props.params.listingId}/getInfo`)
        .then(res=> res.json())
        .then(data=> {
            console.log({"data":data});
            this.setState({
            listingId:data._id,
            title: data.title,
            description: data.description,
            price: data.price,
           // img: data.img,
            category: data.category,
            location:{
            stateName: data.location.stateName,
            city: data.location.city
            },
            images: data.images, 
            userId: data.userID     
          });
        })
        .catch(err => console.log(err, "error fetching Listing"))
    }

    deleteImage = (index) => {
        const images = this.state.images;
        images.splice(index,1); //elimina la imagen a la que pertenezca el index
        this.setState({images:images}) 
    }

    onChange = (e) => {
      this.setState({[e.target.name]:e.target.value}) ;
    }

    onSubmit = (e) => {
        e.preventDefault();
        const listingData={
            listingId: this.state.listingId,
            title: this.state.title,
            description: this.state.description,
            location: {
                stateName: this.state.location.stateName,
                city: this.state.location.city,
            },
            category:this.state.category,
            price: this.state.price,
            images: this.state.images,
            userId:this.state.userId
           // tags: "",
        }

        this.props.updateListing(listingData);
    }
      /* Hay 
      que 
      proteger la
      ROUTEEEEE
      con el ID
      */

  render() {
    return (
        <div>
            <h1>Editar Listado</h1>
            {/*noValidate indica que el form 
        no validara que tipo de dato esta en cada input */}
            <form noValidate onSubmit={this.onSubmit}>
                <div>
                    <h3>Titulo</h3>
                    <input 
                    name = "title"
                    value={this.state.title}
                    onChange={this.onChange}
                    />
                </div>
                <div>
                    <h3>Descripcion</h3>
                    <input 
                    name = "description"
                    value={this.state.description}
                    onChange={this.onChange}
                    />
                </div>
                <div>
                    <h3>Categoria</h3>
                    <input 
                    name = "category"
                    value={this.state.category}
                    onChange={this.onChange}
                    />
                    
                </div>
                <div>
                    <h3>Precio</h3>
                    <input 
                    name = "price"
                    value={this.state.price}
                    onChange={this.onChange}
                    />
                    
                </div>

                <div class="row">
                {this.state.images?.map((item, i)=>{
                  return <div class="col-5 m-2">
                    <img id={`image-${i}`}class="img-fluid img-thumbnail" style={{width:"13em", height:"13em"}} src={item} alt="uploaded-image"/>
                    <button type="button" onClick={()=>{this.deleteImage(i)}} id={`btn-${i}`}>Eliminar</button>
                    
                  </div>
                })
                
                }
                </div>

                <button>Agregar Imagen</button>

                <div>
                    <button type="submit">Guardar</button>
                </div>
            </form>
            
        </div>
    )
  }
}

const WrapperEditListing = props => {
    const navigate = useNavigate();
    const params = useParams();
    /*que pasa aqui?
    Edit: daba error por que en react-router v6 no existe withRouter nor useHistory y 
    useNavigate solo se puede usar en componentes funciones por ser un Hook*/
    return (
        <EditListing
        navigate = {navigate}
        params = {params}
        {...props}
        />
    )
  
  }
  
  EditListing.propTypes = {
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
  };
  
  const mapStateToProps = (state) => ({
    auth: state.auth,
    listing: state.listings,
    errors: state.errors,
  
  });
  
  export default connect(
    mapStateToProps,
    {updateListing}
  )(WrapperEditListing);
  