import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { useNavigate, useParams, Link } from 'react-router-dom';



class ListingTemplate extends Component {
  constructor(){
      super();
    this.state={
      listingId: "",
      title: "",
      description: "",
      price: "",
      img: [],
      category: "",
      country: "",
      stateName: "",
      city: "",
      images:[],
      userId:"",
      errors: {}
    }


  }


  componentDidMount(){
    this.fetchListingInfo();
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  fetchListingInfo= () => {
    fetch(`/api/listados/${this.props.params.id}/getInfo`)
    .then(res=> res.json())
    .then(data=> {this.setState
      ({...this.state,
        listingId:data._id,
        title: data.title,
        description: data.description,
        price: data.price,
       // img: data.img,
        category: data.category,
        country: data.country,
        stateName: data.location.stateName,
        city: data.location.city,
        images: data.images,
        userId: data.userID     
      });
    })
  }

  render() {
    return (
      <div class="container-fluid">
      <p><br/>{this.state.listingId}<br/></p>
        <h3>{this.state.title}</h3>
        <div class="text-center">
          <img class="img-fluid rounded" style={{maxWidth:'70%'}} src={`${this.state.images[0]}`}/>
        </div>
        <p class="border"> Descripcion: {this.state.description}</p>
        <h2>${this.state.price}</h2>
        {/*Las imagenes se deben conseguir de la DB y mapearlas */}
        <p class="border">Categoría: {this.state.category} <br/>Ubicación: {this.state.city + "," + this.state.stateName}</p>
        <Link to={`/user/${this.state.userId}/profile`}>
          <p >Usuario: {this.state.userId}</p>
        </Link>
        
        {/* Navbar */}
      </div>
     
    )
  }
}

const WrapperListingTemplate = props => {
  const navigate = useNavigate();
  const params = useParams();
  /*que pasa aqui?
  Edit: daba error por que en react-router v6 no existe withRouter nor useHistory y 
  useNavigate solo se puede usar en componentes funciones por ser un Hook*/
  return (
      <ListingTemplate
      navigate = {navigate}
      params = {params}
      {...props}
      />
  )

}

ListingTemplate.propTypes = {
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  listing: state.listings,
  errors: state.errors,

});

export default connect(
  mapStateToProps,
)(WrapperListingTemplate);

